$color: #fff;
$fondo: #11cad1;

.RNosotros {
  margin: 1rem 1rem;
  padding: 1rem 1rem;
  color: $color;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.RNosotros img {
  width: 25rem;
  height: 25rem;
  border-radius: 50%;
}

.textR {
  width: 45rem;
  p {
    font-size: 1.3rem;
  }
  .textNosotros {
    font-size: 2.3rem;
    margin-left: 2rem;
    cursor: pointer;
    position: relative;
    &:hover {
      font-size: 2.4rem;
      transition: 1s ease all;
    }
    &::after {
      position: absolute;
      content: "";
      background-image: url("./images/iconoPlayStore.png");
      background-repeat: no-repeat;
      background-size: cover;
      left: -2.1rem;
      top: 0.8rem;
      width: 2rem;
      height: 2rem;
    }
  }
}
@media (max-width: 750px) {
  .RNosotros img {
    width: 20rem;
    height: 20rem;
  }
  .textR p{
    font-size: 1.4rem;
  }
  .textR .textNosotros{
    font-size: 1.8rem;
  }
}
