body {
  margin: 0;
  padding: 0;
  font-family: "Titillium Web", sans-serif;
  background-color: rgb(58, 85, 84);
}
html {
  box-sizing: border-box;
  font-size: 16px;
}
* *:after,
*:before {
  box-sizing: inherit;
}
.header .img img {
  width: 3rem;
  height: 3rem;
  border-radius: 0.3rem;
  margin-left: 1rem;
}
.header {
  top: 0;
  width: 100%;
  max-width: 100%;
  position: fixed;
  color: white;
  height: 3.5rem;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.2rem;
  z-index: 100;
}
.header nav {
  margin-right: 1rem;
}
.header a {
  text-decoration: none;
  color: white;
  margin: 0 0.5rem;
}
.header .header-app {
  flex: 1;
  margin-left: 1rem;
  position: relative;
}

.menuR {
  margin-top: 5rem;
}
.tituloR {
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;
  color: white;
}
.contenedorR {
  width: 100%;
  display: block;
  margin: 0 auto;
  padding: 1.5rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  color: #fff;
}
.contenedorR article {
  width: 15rem;
  height: 17rem;
  border-radius: 2rem;
  margin: 1rem;
  background-color: rgb(100, 167, 255);
  box-shadow: 1px 1px 1px rgba(255, 255, 255, 0.499);
  padding: 0.7rem 0.3rem;
}

.contenedorR article img {
  border-radius: 0.5rem;
  width: 90%;
  display: block;
  margin: auto;
  max-width: 100%;
  height: 8.8rem;
  object-fit: cover;
}

.figureLive {
  width: 40%;
  background-color: #000;
  border-radius: 5rem;
  padding: 1.5rem 0;
}
.RFooter {
  width: 100%;
  background-color: #000;
  color: #fff;
  padding: 1rem 0;
}
.FooterContenedor {
  display: flex;
  width: 100%;
}
.FooterP {
  font-size: 0.9rem;
  margin-left: 4rem;
  margin-top: 1rem;
}
.lista p {
  margin: 0;
  cursor: pointer;
}
.lista p:hover {
  font-weight: 700;
}
.FooterRedes {
  display: flex;
  margin-top: 1rem;
  margin-left: 1rem;
}
.FooterRedes p {
  font-size: 2rem;
  margin-top: 0;
  margin-right: 1rem;
  font-weight: 700;
}
.img-footer img {
  width: 4.5rem;
  height: 4.5rem;
  margin: 0 0.3rem;
  object-fit: cover;
  cursor: pointer;
}
.menu {
  display: none;
  font-size: 2rem;
  cursor: pointer;
  position: absolute;
  right: 1rem;
  top: 1rem;
  color: #fff;
  z-index: 1000;
  position: fixed;
}
@media screen and (max-width: 750px) {
  .menu {
    display: block;
  }
  .header .img {
    display: none;
  }
  .header-app {
    display: none;
  }
  .header {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.675);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header.hidden {
    display: none;
  }
  .header nav {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.6rem;
  }
  .header nav a {
    margin: 1rem 0;
  }
  .contenedorR article {
    width: 18rem;
    height: 20rem;
    font-size: 1.2rem;
  }
  .contenedorR article img {
    height: 11rem;
  }
  .figureLive {
    width: 90%;
  }
  .FooterRedes {
    flex-wrap: wrap;
    margin: 0;
  }
  .FooterContenedor {
    justify-content: center;
    align-items: center;
  }
}
